import React from "react";
import { FaPlus } from "react-icons/fa";
import Tooltip from "../Tooltip/Tooltip";

const index = ({ search, onClick, addPermission }) => {
  return (
    <div className="text-center justify-content-center no-data">
      <h4 className="fw-normal mb-3">No data to Display</h4>
      {!search && addPermission && (
        <h6>
          Please{" "}
          <Tooltip content="Add">
            <button
              onClick={onClick}
              className="btn btn-primary waves-effect waves-light btn-sm mx-2 p-1 nodata-btn"
            >
              <FaPlus className="nodata-icon" />
              &nbsp;Add
            </button>
          </Tooltip>{" "}
          some...
        </h6>
      )}
    </div>
  );
};

export default index;
