import axiosInstance from "../../utils/axios";

//obtaining the paginated data
export const getUser = (postsPerPage) =>
  axiosInstance.get(
    `api/v1/user-app/user?offset=0&limit=${postsPerPage}&ordering=-id`
  );

//obtaining all data
export const getAllUser = () =>
  axiosInstance.get(`api/v1/user-app/user?ordering=-id`);

export const getCurrentUser = (decoded) =>
  axiosInstance.get(`api/v1/user-app/user/${decoded.user_id}`);
//obtaining the previous page data from paginated data
export const getPrevious = (previous) => axiosInstance.get(previous);

//obtaining the next page data from paginated data
export const getNext = (next) => axiosInstance.get(next);

//obtaining the particular page data from paginated data
export const getPageUser = (number, postsPerPage) =>
  axiosInstance.get(
    `api/v1/user-app/user?offset=${
      (number - 1) * postsPerPage
    }&limit=${postsPerPage}&ordering=-id`
  );

//creating function
export const createUser = (body) =>
  axiosInstance.post(`api/v1/user-app/user`, body);

//deleting function
export const deleteUser = (id) =>
  axiosInstance.delete(`api/v1/user-app/user/${id}`);

//update function
export const updateUser = (id, body) =>
  axiosInstance.patch(`api/v1/user-app/update-user/${id}`, body);

//searching function
export const handleSearch = (search, postsPerPage) =>
  axiosInstance.get(
    `api/v1/user-app/user?offset=0&limit=${postsPerPage}&search=${search}`
  );

// checking the redundant data
export const checkRedundantData = (e, cancelToken) =>
  axiosInstance.get(`api/v1/user-app/user?username=${e.target.value.trim()}`, {
    cancelToken: cancelToken.token,
  });
//deleting the image
export const handleDelete = (id, body) =>
  axiosInstance.patch(`api/v1/user-app/user/${id}`, body);
//change password
export const changePassword = (body, id) =>
  axiosInstance.get(`api/v1/user-app/change_password/${id}`);
