import React, { useState } from "react";
import { FaEdit } from "react-icons/fa";
import { useDispatch, useSelector } from "react-redux";
import { getPageUser, getPrevious, getNext } from "../../Redux/User/thunk";
import Status from "../../Component/Status";
import Tooltip from "../../Component/Tooltip/Tooltip";
import { userConstants } from "../../Redux/User/constants";
import PaginationBlock from "../../Component/PaginationBlock";
import NoData from "../../Component/NoData/";
import { useHistory } from "react-router-dom";
const User = ({
  currentPage,
  postsPerPage,
  setCurrentPage,
  count,
  setShowModal,
  maxPageNumberLimit,
  setMaxPageNumberLimit,
  minPageNumberLimit,
  setMinPageNumberLimit,
  search,
}) => {
  // props
  const history = useHistory();
  const permissions = useSelector((state) => state.auth.permissions);
  const is_superuser = useSelector((state) => state.auth.is_superuser);
  const users = useSelector((state) => state.user.users);
  const next = useSelector((state) => state.user.next);
  const previous = useSelector((state) => state.user.previous);
  const dispatch = useDispatch();

  // for pagination
  const [pageNumberLimit] = useState(5);

  //change page
  const paginate = (number) => {
    dispatch(getPageUser({ number, postsPerPage }));
    setCurrentPage(number);
  };

  //handle Click
  const handleClick = (type) => {
    if (type === "previous") {
      dispatch(getPrevious(previous));
      setCurrentPage((prevState) => prevState - 1);
      if ((currentPage - 1) % pageNumberLimit === 0) {
        setMaxPageNumberLimit(maxPageNumberLimit - pageNumberLimit);
        setMinPageNumberLimit(minPageNumberLimit - pageNumberLimit);
      }
    } else if (type === "next") {
      dispatch(getNext(next));
      setCurrentPage((prevState) => prevState + 1);
      if (currentPage + 1 > maxPageNumberLimit) {
        setMaxPageNumberLimit(maxPageNumberLimit + pageNumberLimit);
        setMinPageNumberLimit(minPageNumberLimit + pageNumberLimit);
      }
    }
  };

  //pagination end

  //sorting end

  //edit function for the province
  const handleEdit = (id, postsPerPage) => {
    dispatch({
      type: userConstants.USER_EDIT_SUCCESS,
      payload: id,
      postsPerPage: postsPerPage,
    });
    history.push("/edit-user");
  };
  const addPermission =
    permissions?.includes("add_user") || is_superuser ? true : false;
  return (
    <>
      {users?.length > 0 ? (
        <div className="mt-2">
          <div className="table-scrollable">
            <table className="table table-hover table-bordered text-center">
              <thead>
                <tr>
                  <th scope="col" style={{ width: "8%" }}>
                    S.N
                  </th>
                  <th scope="col">
                    User Name
                    {/* <BsArrowUpDown
                      onClick={() => stringSort(decending, setDecending, users)}
                    /> */}
                  </th>
                  <th scope="col">Email</th>
                  <th scope="col">Branch</th>
                  <th scope="col">Employee ID</th>
                  <th scope="col">Status</th>
                  {(is_superuser || permissions?.includes("update_user")) && (
                    <th scope="col">Action</th>
                  )}
                </tr>
              </thead>
              <tbody>
                {users?.length > 0 &&
                  users.map((user, i) => {
                    const {
                      id,
                      username,
                      email,
                      user_branch,
                      employee_id,
                      is_active,
                    } = user;
                    return (
                      <tr key={id}>
                        <th scope="row" style={{ width: "8%" }}>
                          {postsPerPage * (currentPage - 1) + (i + 1)}
                        </th>
                        <td>{username}</td>
                        <td>{email}</td>
                        <td>{user_branch?.name}</td>
                        <td>{employee_id}</td>

                        <td>
                          <Status active={is_active} />
                        </td>
                        {(is_superuser ||
                          permissions?.includes("delete_user") ||
                          permissions?.includes("update_user")) && (
                          <td>
                            {(is_superuser ||
                              permissions?.includes("update_user")) && (
                              <Tooltip content="Edit">
                                <button
                                  className="btn btn-sm btn-edit btn-primary mx-1"
                                  onClick={() => handleEdit(id)}
                                >
                                  <FaEdit />
                                </button>
                              </Tooltip>
                            )}
                          </td>
                        )}
                      </tr>
                    );
                  })}
              </tbody>
            </table>
          </div>
          {count > 0 && (
            <PaginationBlock
              currentPage={currentPage}
              postsPerPage={postsPerPage}
              count={count}
              paginate={paginate}
              handleClick={handleClick}
              setCurrentPage={setCurrentPage}
              minPageNumberLimit={minPageNumberLimit}
              maxPageNumberLimit={maxPageNumberLimit}
            />
          )}
        </div>
      ) : (
        <NoData
          search={search}
          onClick={() => setShowModal(true)}
          addPermission={addPermission}
        />
      )}
    </>
  );
};

export default React.memo(User);
