import React, { useState, useEffect, lazy } from "react";
import { useSelector, useDispatch } from "react-redux";
import ListingSkeleton from "../../Component/Skeleton/ListingSkeleton";
import { getUser, handleSearch } from "../../Redux/User/thunk";
import User from "./User";
import { defaultLimit } from "../../utils/defaultLimit";
import { FaPlus } from "react-icons/fa";
import PaginationLimit from "../../Component/Pagination/PaginationLimit";
import Tooltip from "../../Component/Tooltip/Tooltip";
import UpdatedSkeleton from "../../Component/Skeleton/UpdatedSkeleton";
import { useHistory } from "react-router-dom";

const UserListing = () => {
  const history = useHistory();
  const permissions = useSelector((state) => state.auth.permissions);
  const is_superuser = useSelector((state) => state.auth.is_superuser);
  const users = useSelector((state) => state.user.users);
  const loading = useSelector((state) => state.user.loading);
  const loading_updated = useSelector((state) => state.user.loading_updated);
  const count = useSelector((state) => state.user.count);
  const edit = useSelector((state) => state.user.edit);
  const search = useSelector((state) => state.search.search);
  const dispatch = useDispatch();

  // for pagination
  const [currentPage, setCurrentPage] = useState(1);
  const [maxPageNumberLimit, setMaxPageNumberLimit] = useState(5);
  const [minPageNumberLimit, setMinPageNumberLimit] = useState(0);
  const [postsPerPage, setPostsPerPage] = useState(10);

  //pagination end
  //loading all the food
  useEffect(() => {
    if (postsPerPage === 0) {
      if (search === "") {
        dispatch(getUser(count));
      } else {
        dispatch(handleSearch(search, count));
      }
    } else {
      if (search === "") {
        if (postsPerPage === defaultLimit) {
          dispatch(getUser(postsPerPage));
        } else {
          setCurrentPage(1);
          dispatch(getUser(postsPerPage));
        }
      } else {
        setCurrentPage(1);
        setMaxPageNumberLimit(5);
        setMinPageNumberLimit(0);
        dispatch(handleSearch(search, postsPerPage));
      }
    }
  }, [dispatch, postsPerPage, search]);

  return (
    <>
      <div className="card">
        <div className="header-content">
          <h4 className="mb-sm-0 fw-normal">User</h4>
        </div>
        <div className="card-body">
          <div className="row m-0 p-0">
            {users?.length > 0 && (
              <>
                <div className="col-6">
                  <PaginationLimit
                    postsPerPage={postsPerPage}
                    setPostsPerPage={setPostsPerPage}
                  />
                </div>
                <div className="col-6 text-right">
                  {(is_superuser || permissions?.includes("add_user")) && (
                    <Tooltip content="Add">
                      <button
                        type="button"
                        className="btn btn-primary waves-effect waves-light btn-sm float-end"
                        onClick={() => history.push("/create-user")}
                      >
                        <FaPlus className="mb-1" />
                        &nbsp;Add
                      </button>
                    </Tooltip>
                  )}
                </div>
              </>
            )}
          </div>
          {loading && <ListingSkeleton />}
          {loading_updated && <UpdatedSkeleton />}
          {!loading && !loading_updated && (
            <User
              currentPage={currentPage}
              setCurrentPage={setCurrentPage}
              postsPerPage={postsPerPage}
              setPostsPerPage={setPostsPerPage}
              count={count}
              maxPageNumberLimit={maxPageNumberLimit}
              setMaxPageNumberLimit={setMaxPageNumberLimit}
              minPageNumberLimit={minPageNumberLimit}
              setMinPageNumberLimit={setMinPageNumberLimit}
              search={search}
            />
          )}
        </div>
      </div>
    </>
  );
};

export default UserListing;
