//get particular page
export const getPageUser =
  ({ number, postsPerPage }) =>
  async (dispatch) => {
    try {
      dispatch(action.loadingAction());
      const { data } = await API.getPageUser(number, postsPerPage);

      dispatch(action.getUserSuccessAction(data));
    } catch (error) {
      dispatch(action.getUserFailAction(error));
      errorFunction(error);
    }
  };

export const getCurrentUser = (token) => async (dispatch) => {
  try {
    dispatch(action.loadingAction());
    const decoded = token && jwt_decode(token);
    const { data } = await API.getCurrentUser(decoded);
    dispatch(action.getUserInfoSuccessAction(data));
  } catch (error) {
    dispatch(action.getUserInfoFailAction(error));
    errorFunction(error);
  }
};
export const createUser =
  (
    {
      first_name,
      middle_name,
      last_name,
      email,
      username,
      employee_id,
      is_active,
      user_branch,
      group,
      gender,
      password,
      confirm_password,
      history,
    },
    currentPage
  ) =>
  async (dispatch) => {
    try {
      const body = JSON.stringify({
        first_name,
        middle_name,
        last_name,
        email,
        username,
        employee_id,
        group,
        is_active,
        user_branch,
        gender,
        password,
        confirm_password,
      });
      dispatch(action.loadingAction());
      await API.createUser(body);
      // dispatch(action.createUserSuccessAction(data));
      successFunction("User created successfully ");
      dispatch(getPageUser({ number: currentPage, postsPerPage: 10 }));
      history.push("/user");
    } catch (error) {
      dispatch(action.createUserFailAction(error));
      errorFunction("Failed to create user.");
    }
  };
// Delete  bank
export const deleteUser = (id) => async (dispatch) => {
  try {
    dispatch(action.loadingAction());
    const { data } = await API.deleteUser(id);
    dispatch(action.deleteUserSuccessAction(data));
    dispatch(getUser());
    successFunction("Bank deleted Successfully");
  } catch (error) {
    dispatch(action.deleteUserFailAction(error));
    errorFunction("Failed to delete Bank ");
  }
};
//update
export const updateUser =
  (id, values, history, currentPage = undefined) =>
  async (dispatch) => {
    const {
      first_name,
      middle_name,
      last_name,
      email,
      username,
      employee_id,
      user_branch,
      group,
      gender,
      is_active,
      remarks,
    } = values;
    try {
      dispatch(action.loadingUpdatedAction());

      const body = JSON.stringify({
        first_name,
        middle_name,
        last_name,
        email,
        username,
        employee_id,
        user_branch,
        group,
        gender,
        is_active,
        remarks,
      });
      const { data } = await API.updateUser(id, body);

      dispatch(getPageUser({ number: 1, postsPerPage: 10 }));
      successFunction("User Updated Successfully ");
      history.push("/user");
    } catch (error) {
      dispatch(action.updatedUserFailAction(error));
      errorFunction("Failed to Update User");
    }
  };
//handle Search
export const handleSearch = (search, postsPerPage) => async (dispatch) => {
  try {
    dispatch(action.loadingAction());
    const { data } = await API.handleSearch(search, postsPerPage);
    dispatch(action.getUserSuccessAction(data));
  } catch (error) {
    dispatch(action.getUserFailAction(error));
    errorFunction(error);
  }
};
//delete photo
export const deletePhoto = (id) => async (dispatch) => {
  try {
    const body = JSON.stringify({ photo: "" });
    const { data } = await API.handleDelete(id, body);
    dispatch(action.deletePhotoSuccessAction(data));
    successFunction(data);
  } catch (error) {
    dispatch(action.deletePhotoFailAction(error));
    errorFunction(error);
  }
};
